import React from 'react';
import './Addnew.scss';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';
import Alert from '@material-ui/lab/Alert';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';
import { history } from '../../../helpers';
import { connect } from 'react-redux'
import TextField from "@material-ui/core/TextField";

class Addnew extends React.Component
{
	constructor(props) {
	      super(props);
	      this.state = {
	      	  id: '',
	          title: '',
	          description: '',
	          date: '',
	          time: '',
	          loading:false,
	          showingAlert: false,
	      }

	      this.handleChange = this.handleChange.bind(this);
	      this.handleSignUp = this.handleSignUp.bind(this);
	}
	
	componentDidMount() {
	      var today = new Date();
	      var date = today.getFullYear() + '-' + (today.getMonth()+1) + '-' + today.getDate();
          var time = today.getHours()+':'+ today.getMinutes();
          var number = Math.random()
          number.toString(36); 
          var id = number.toString(36).substr(2, 9);
          this.setState({ date: date,time:time,id:id });
          
	}
	handleChange(e) {
        const { name, value } = e.target;
        this.setState({ [name]: value });
    }
    
    handleSignUp(e) {

    	e.preventDefault();
    	setTimeout(() => {
	    	var today = new Date();
		    var currentdate = today.getFullYear() + '-' + (today.getMonth()+1) + '-' + today.getDate();
	        var currenttime = today.getHours()+':'+ today.getMinutes();
	    	const { id, title, description, date, time } = this.state;
		    const postpara = { id:id, title: title, description: description, date: date, time:time, created_date:currentdate,created_time:currenttime };
		    this.props.dispatch({ type: 'ADD',item:postpara });
		    
	           this.props.history.push("/home");
        }, 500); 
	   // this.setState({ loading: false, showingAlert: true });
    }
     
	render(){
		const { title, description, date, time } = this.state;
		return(
		<div>
			<div className="mainbody signup-page">
			   <div className="padding">
                   <Grid container>
                       <Grid item xs={2}></Grid>
                       <Grid item xs={8}>
	                       <Card>
	                       <h4>Add New</h4>
	                        { this.state.showingAlert ?
		                        	<div>
		                                <Alert severity="error" onClose={() => { this.setState({ showingAlert: false }) }}>Registration failed. Please try again!</Alert>
		                                <p></p>
		                            </div>
                            : '' }
	                        <ValidatorForm ref="form" onSubmit={this.handleSignUp} method="POST">
	                            <TextValidator label="Title" variant="outlined" name="title" value={title} onChange={this.handleChange} validators={['required']} errorMessages={['This field is required']}/>
	                            
                                <TextValidator label="Description" variant="outlined" name="description" value={description} onChange={this.handleChange} validators={['required']} errorMessages={['Please enter description']}/>
		                         
		                        <TextValidator type="date" variant="outlined" name="date" value={date} defaultValue={date} InputLabelProps={{ shrink: true }} onChange={this.handleChange} validators={['required']} errorMessages={['Please enter date']}/>
		                        
		                        <TextValidator type="time" variant="outlined" name="time" value={time} defaultValue={time} InputLabelProps={{ shrink: true }} onChange={this.handleChange} validators={['required']} errorMessages={['Please enter time']}/>

		                        <Button type="submit" variant="contained" color="primary" >Save</Button>
                            </ValidatorForm>  
		                       
		                   </Card>
                       </Grid>
                       <Grid item xs={2}></Grid>
                   </Grid>
			   </div>
			</div>
		</div>	
		);
	}
}
const mapStateToProps = state => {
    return {
    } 
}
export default connect(mapStateToProps,null)(Addnew);
